@import url(https://fonts.googleapis.com/css?family=Fira+Sans|Raleway|Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/*
font-family: 'Roboto', sans-serif;
font-family: 'Fira Sans', sans-serif;
font-family: 'Raleway', sans-serif;
*/

*, *:before, *:after {
    box-sizing: border-box;
}

html, body {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: 'Fira Sans', sans-serif;
}

#main-container {
    display: flex;
    flex-flow: row;
    width: 100%;
    height: 100%;
    padding: 0.4%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#left-container {
    display: flex;
    flex-flow: column;
    width: 60%;
    height: 100%;
}

#chart-container{
    width: 100%;
    height: 85%;
}
#chart-controls{
    width: 100%;
    height: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row;
    flex-wrap: wrap;
}

#input-container{
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#plus-container{
    display: flex;
    flex-flow: row;
    flex-wrap: no-wrap;
    height: 100%;
    width: 10%;
    margin: 0 0.5%;
}

#plusInput{
    width: 50%;
    height: 100%;
}
#plusButton{
    background: rgb(34, 175, 34); /* Green */
    width: 50%;
    height: 100%;
    padding: 0;
}

#minus-container{
    display: flex;
    flex-flow: row;
    flex-wrap: no-wrap;
    height: 100%;
    width: 10%;
    margin: 0 0.5%;
}

#minusInput{
    width: 50%;
    height: 100%;
}
#minusButton{
    background: rgb(220, 20, 60); /* Red */
    width: 50%;
    height: 100%;
    padding: 0;
}

/* Remove the up/down arrows from the number inputs */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

input {
    text-align: center;
    text-decoration: none;
    font-size: 2rem;
    font-weight: bold;
    border: 0.1px solid #555555;
}

.width-5 {
    max-width: 5%;
}

button {
    background: rgb(255, 165, 0); /* Orange */
    border: none;
    color: white;
    width: 20%;
    height: 50%;
    text-align: center;
    text-decoration: none;
    font-size: 2rem;
    font-weight: bold;
}

#submitData {
    margin: 1%;
}

#addPostit {
    margin: 1%;
}

button:hover{
    background: rgb(255, 140, 0); /* Dark Orange */
}

#plusButton:hover{
    background: rgb(34, 130, 34);
}

#minusButton:hover {
    background: rgb(175, 20, 60);
}
